import React, { useState, useEffect } from 'react';
import 'aos/dist/aos.css';
import AOS from 'aos';

import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Popover from 'react-bootstrap/Popover';

import htmlImg from "../imagens/icons/skillsimg/icons8-html-50.png";
import BootsImg from "../imagens/icons/skillsimg/icons8-bootstrap-50.png";
import CssImg from "../imagens/icons/skillsimg/icons8-css-50.png";
import GitImg from "../imagens/icons/skillsimg/icons8-git-50.png";
import GitHImg from "../imagens/icons/skillsimg/icons8-github-50.png";
import JsImg from "../imagens/icons/skillsimg/icons8-javascript-50.png";
import MysqlImg from "../imagens/icons/skillsimg/icons8-mysql-50.png";
import ReactImg from "../imagens/icons/skillsimg/icons8-nativo-de-reagir-50.png";
import SassImg from "../imagens/icons/skillsimg/icons8-sass-50.png";
import JavaImg from "../imagens/icons/skillsimg/icons8-logo-java-coffee-cup-50.png";
import KotlinImg from "../imagens/icons/skillsimg/icons8-kotlin-50.png";
import SqlImg from "../imagens/icons/skillsimg/icons8-sqlite-50.png";
import PythonImg from "../imagens/icons/skillsimg/icons8-python-50.png";

const Skills = (props) => {
  const [popoverHeader, setPopoverHeader] = useState("");
  const [popoverBody, setPopoverBody] = useState("");


  useEffect(() => {
    AOS.init({
      duration: 100, // Durao da animao em milissegundos
      easing: 'ease-in', // Tipo de easing
      once: false, // A animao ocorre apenas uma vez
      offset: 40, // Offset da animao
      delay: 0, // Atraso da animao
      mirror: true, // Anima ao rolar para fora tambm
    });

    // Atualizar as animaes sempre que o componente atualizar
    AOS.refresh();
  }, []);


  // Recebe o parametro do tema do BodyAll (Claro ou Escuro/ True or False)
  const changeTheme = props.changeTheme;

  const changeNumberPopover = (number) => {
    changePopover(number);
  };

  const changePopover = (number) => {
    switch (number) {
      case 1:
        setPopoverHeader("HTML");
        setPopoverBody("HyperText Markup Language  a linguagem de marcao padro para a criao de pginas web.  a base estrutural de qualquer site na Internet e  interpretado pelos navegadores para renderizar e exibir contedo na web.");
        break;
      case 2:
        setPopoverHeader("CSS");
        setPopoverBody("Cascading Style Sheets  uma linguagem de estilo utilizada para definir a apresentao e o layout de documentos HTML (ou XML). Ela permite controlar o aspecto visual de uma pgina web, incluindo cores, fontes, espaamento, posicionamento de elementos e efeitos visuais.");
        break;
      case 3:
        setPopoverHeader("JavaScript");
        setPopoverBody("JavaScript  uma linguagem de programao interpretada estruturada, de script em alto nvel com tipagem dinmica fraca e multiparadigma.");
        break;
      case 4:
        setPopoverHeader("React");
        setPopoverBody("React  uma biblioteca JavaScript de cdigo aberto e amplamente utilizada para a criao de interfaces de usurio interativas e responsivas.");
        break;
      case 5:
        setPopoverHeader("Bootstrap");
        setPopoverBody("Bootstrap  um framework front-end popular e de cdigo aberto que oferece um conjunto abrangente de componentes, estilos e ferramentas para o desenvolvimento de interfaces web responsivas e esteticamente agradveis.");
        break;
      case 6:
        setPopoverHeader("Sass");
        setPopoverBody("Syntactically Awesome Stylesheets  uma linguagem de extenso para folhas de estilo CSS. Ela acrescenta recursos poderosos ao CSS tradicional, tornando o processo de estilizao mais eficiente, modular e fcil de manter.");
        break;
      case 7:
        setPopoverHeader("Java");
        setPopoverBody("Java  uma linguagem de programao orientada a objetos desenvolvida na dcada de 90 por uma equipe de programadores chefiada por James Gosling, na empresa Sun Microsystems, que em 2008 foi adquirido pela empresa Oracle Corporation.");
        break;
      case 8:
        setPopoverHeader("Kotlin");
        setPopoverBody("Kotlin  uma linguagem de programao multiplataforma, orientada a objetos e funcional, concisa e estaticamente tipada, desenvolvida pela JetBrains em 2011, que compila para a Mquina virtual Java e que tambm pode ser traduzida para a linguagem JavaScript e compilada para cdigo nativo.");
        break;
      case 9:
        setPopoverHeader("SQLite");
        setPopoverBody("SQLite  uma biblioteca em linguagem C que implementa uma base de dados SQL embutida. Programas que usem a biblioteca SQLite podem ter acesso a banco de dados SQL sem executar um processo SGBD separado.");
        break;
      case 10:
        setPopoverHeader("Python");
        setPopoverBody("Python  uma linguagem de programao de alto nvel, interpretada de script, imperativa, orientada a objetos, funcional, de tipagem dinmica e forte. Foi lanada por Guido van Rossum em 1991.");
        break;
      case 11:
        setPopoverHeader("Git");
        setPopoverBody("Git  um sistema de controle de verso distribudo amplamente utilizado no desenvolvimento de software.");
        break;
      case 12:
        setPopoverHeader("GitHub");
        setPopoverBody("O GitHub  uma plataforma baseada na web que utiliza o sistema de controle de verso Git para hospedar, colaborar e gerenciar projetos de software.");
        break;
      case 13:
        setPopoverHeader("MySQL");
        setPopoverBody("MySQL  um sistema de gerenciamento de banco de dados relacional (RDBMS) amplamente utilizado, que fornece uma maneira eficiente e confivel de armazenar, gerenciar e recuperar dados.");
        break;


      default:
        setPopoverHeader("");
        setPopoverBody("");
        break;
    }
  };

  const popovers = (
    <Popover id="Popover">
      <Popover.Header as="h3">{popoverHeader}</Popover.Header>
      <Popover.Body>{popoverBody}</Popover.Body>
    </Popover>
  );

  return (
    <div className={changeTheme ? "Container-Texts-LightTheme" : "Container-Texts-DarkTheme"}>
      <div className="skills-container">
        <h1>Habilidades</h1>
        <div className="skills-list">
          {/* Animaes de scroll dos icons */}
          {[htmlImg, CssImg, JsImg, ReactImg, BootsImg, SassImg, JavaImg, KotlinImg, SqlImg, PythonImg, GitImg, GitHImg, MysqlImg].map((imgSrc, index) => (
            <div key={index} className="skill-item">
              <div>
                <div data-aos="zoom-in">
                  <OverlayTrigger trigger="hover" placement="auto" overlay={popovers}>
                    <button onMouseEnter={() => changeNumberPopover(index + 1)}>
                      <img src={imgSrc} alt={`Habilidade ${index + 1}`} />
                    </button>
                  </OverlayTrigger>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Skills;